<template>
  <div class="print-container">
    <!--   -->
    <div class="print-layout" v-if="assembly">
      <!--   -->
      <div class="print-page main-page">
        <div class="header">
          <p>   {{ assembly.main.head_name }}</p>
          <p><strong> :</strong> {{ assembly.main.order_num }}, <strong>:</strong> {{ assembly.main.position }}</p>
          <p><strong>:</strong> {{ assembly.main.count }}</p>

          <table class="operations-table">
            <thead>
            <tr>
              <th width="40%"></th>
              <th width="10%"> <br> /</th>
              <th width="10%"> <br> /</th>
              <th> </th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="op in assembly.main.operations" :key="op.operation_name">
              <td>{{ op.operation_label }}</td>
              <td>{{ op.value.toFixed(3) }}</td>
              <td>{{ op.minutes }}</td>
              <td></td>
            </tr>
            <tr class="total-row">
              <td colspan="1" class="text-right"><strong>:</strong></td>
              <td>{{ assembly.main.total_time.toFixed(3) }}</td>
              <td>{{ Math.round(assembly.main.total_time * 60) }}</td>
            </tr>
            </tbody>
          </table>

          <!--   -->
          <AddictionOperation />
        </div>
      </div>

      <!--        -->
      <div
          v-for="sub in assembly.subs"
          :key="sub.id"
          class="print-page sub-page"
      >
        <div class="header">
        <p>   {{ sub.head_name }}</p>
        <p><strong> :</strong> {{ sub.order_num }}, <strong>:</strong> {{ assembly.main.position }}</p>
        <p><strong>:</strong> {{ sub.count }}</p>

        <table class="operations-table">
          <thead>
          <tr>
            <th width="40%"></th>
            <th width="10%"> <br> /</th>
            <th width="10%"> <br> /</th>
            <th> </th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="op in sub.operations" :key="op.operation_name">
            <td>{{ op.operation_label }}</td>
            <td>{{ op.value.toFixed(3) }}</td>
            <td>{{ op.minutes }}</td>
            <td></td>
          </tr>
          <tr class="total-row">
            <td colspan="1" class="text-right"><strong>:</strong></td>
            <td>{{ sub.total_time.toFixed(3) }}</td>
            <td>{{ Math.round(sub.total_time * 60) }}</td>
          </tr>
          </tbody>
        </table>

        <AddictionOperation />
        </div>
      </div>

      <!--   -->
      <div class="print-controls">
        <button @click="print" class="btn-print"></button>
        <button
            v-if="assembly.main && assembly.main.status !== 'cancel'"
            @click="confirmCancel"
            class="btn-cancel"
            :disabled="loadingCancel"
        >
          {{ loadingCancel ? '...' : ' ' }}
        </button>
      </div>
    </div>

    <!--  -->
    <div v-else-if="loading" class="loading">
       ...
    </div>

    <!--  -->
    <div v-else class="error">
         .
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { useRoute } from 'vue-router';

import AddictionOperation from "@/components/AddictionOperation.vue";
import axios from "axios";

const route = useRoute();
const assembly = ref(null);
const loading = ref(true);

onMounted(async () => {
  const id = route.params.id;
  if (!id) {
    alert("  ID");
    return;
  }

  try {
    //      rootId
    const itemRes = await fetch(`http://localhost:8080/api/orders/order-norm/${id}`);
    if (!itemRes.ok) throw new Error(`  : ${itemRes.status}`);
    const itemData = await itemRes.json();

    const item = Array.isArray(itemData) ? itemData[0] : itemData;

    console.log("ITEM", item)

    //  rootId
    let rootId;
    if (item.part_type === 'main') {
      rootId = id;
    } else if (item.parent_product_id) {
      rootId = item.parent_product_id;
    } else {
      rootId = id;
    }

    //   
    const assemblyRes = await fetch(`http://localhost:8080/api/orders/order-norm/${rootId}`);
    if (!assemblyRes.ok) throw new Error(`   : ${assemblyRes.status}`);
    const data = await assemblyRes.json();

    //   
    let dataArray;
    if (Array.isArray(data)) {
      dataArray = data;
    } else if (data && typeof data === 'object') {
      dataArray = [data];
    } else {
      throw new Error(' ');
    }

    //  main  subs
    const main = dataArray.find(item => item.part_type === 'main');
    const subs = dataArray.filter(item => item.part_type === 'sub');

    //  main     
    const finalMain = main || (dataArray.length > 0 ? dataArray[0] : null);

    if (!finalMain) {
      throw new Error('   ');
    }

    assembly.value = {
      main: finalMain,
      subs: subs
    };
  } catch (err) {
    console.error(' :', err);
  } finally {
    loading.value = false;
  }
});

const loadingCancel = ref(false);

//   
const confirmCancel = () => {
    cancelOrder(assembly.value.main);
};

//   
const cancelOrder = async (order) => {
  if (!order) return;

  loadingCancel.value = true;
  try {
    const response = await axios.post('http://localhost:8080/api/orders/cancel', {
      root_product_id: order.id,
    });

    if (response.status === 200) {
      //  
      order.status = 'cancel';
      alert('  .');
    } else {
      throw new Error('API   ');
    }
  } catch (error) {
    console.error('   :', error);
    let message = '   .';
    if (error.response?.data?.message) {
      message += '\n' + error.response.data.message;
    }
    alert(message);
  } finally {
    loadingCancel.value = false;
  }
};

function print() {
  const printLayout = document.querySelector('.print-layout');
  if (!printLayout) {
    alert('   ');
    return;
  }

  //  ,    
  const contentClone = printLayout.cloneNode(true);

  //       
  const controls = contentClone.querySelector('.print-controls');
  if (controls) {
    controls.remove();
  }

  //   HTML  
  const contentHtml = contentClone.innerHTML;

  const printHtml = `
    <!DOCTYPE html>
    <html>
      <head>
        <meta charset="utf-8">
        <title> </title>
        <style>
          body {
            font-family: 'Segoe UI', Arial, sans-serif;
            font-size: 13px;
            line-height: 1.4;
            padding: 15px;
            margin: 0;
            color: #000;
            background: #fff;
            -webkit-print-color-adjust: exact;
            print-color-adjust: exact;
            width: 210mm;
            margin: 0 auto;
          }
          .print-page {
            break-inside: avoid;
            page-break-inside: avoid;
            margin-bottom: 12px;
          }
          .header p {
            margin: 2px 0;
            line-height: 1.2;
          }
          table {
            width: 100%;
            border-collapse: collapse;
            margin: 4px 0;
            font-size: 12px;
          }
          th, td {
            border: 1px solid #000;
            padding: 4px 6px;
            text-align: left;
          }
          th {
            background-color: #f2f2f2;
            font-weight: bold;
          }
          .total-row {
            background-color: #f9f9f9;
            font-weight: bold;
          }
          .additional-ops {
            margin-top: 6px;
            break-inside: avoid;
            page-break-inside: avoid;
          }
          @media print {
            @page {
              margin: 1cm;
              size: A4 portrait;
            }
            body {
              -webkit-print-color-adjust: exact;
            }
          }
        </style>
      </head>
      <body onload="window.focus(); window.print();">
        <div class="print-container">
          ${contentHtml}
        </div>
      </body>
    </html>
  `;

  //    about:blank ( ,   )
  const printWindow = window.open('about:blank', '_blank', 'width=1000,height=800,scrollbars=yes');

  //  :  ?
  if (!printWindow) {
    alert('   .         .');
    return;
  }

  try {
    //      
    printWindow.document.open();
    printWindow.document.write(printHtml);
    printWindow.document.close();

    //  :  onload  
    setTimeout(() => {
      if (!printWindow.closed) {
        printWindow.focus();
      }
    }, 250);
  } catch (e) {
    console.error('    :', e);
    alert('      .       Ctrl+P.');
    if (!printWindow.closed) printWindow.close();
  }
}
</script>

<style>
.print-container {
  font-family: 'Segoe UI', Tahoma, Verdana, sans-serif;
  max-width: 900px;
  margin: 10px auto;
  padding: 10px;
  background: #fff;
}

.header {
  margin-bottom: 4px;
}

.header p {
  margin: 2px 0;
  line-height: 1.2;
}

.operations-table {
  width: 100%;
  border-collapse: collapse;
  margin: 4px 0;
  font-size: 11px;
}

.operations-table th,
.operations-table td {
  border: 1px solid #000;
  padding: 3px 5px;
  text-align: left;
}

.operations-table th {
  background-color: #f2f2f2;
  font-weight: 600;
  white-space: nowrap;
  font-size: 11px;
}

.total-row {
  background-color: #f9f9f9;
  font-weight: bold;
}

.text-right {
  text-align: right;
}

.print-controls {
  text-align: center;
  margin: 20px 0 10px;
}

.btn-print {
  padding: 8px 16px;
  font-size: 14px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.btn-print:hover {
  background: #0056b3;
}

.loading,
.error {
  text-align: center;
  font-size: 14px;
  padding: 20px;
}
</style>

<!--    -->
<style scoped>
@media print {
  .print-controls {
    display: none;
  }

  .print-container {
    margin: 0;
    padding: 6px;
    font-size: 10px;
    visibility: visible;
    height: auto !important;
  }

  .header {
    break-inside: avoid;
    page-break-inside: avoid;
  }

  .header p {
    margin: 1px 0;
    line-height: 1.1;
  }

  .operations-table {
    margin: 3px 0;
    break-inside: avoid;
    page-break-inside: avoid;
    font-size: 10px;
  }

  .operations-table th,
  .operations-table td {
    padding: 2px 4px;
    font-size: 9px;
  }

  /*     .  */
  .additional-ops {
    break-inside: avoid;
    page-break-inside: avoid;
    margin-top: 6px;
  }

  .additional-ops-label {
    font-size: 11px;
    margin: 0;
    padding: 0;
  }

  /* ,      */
  .main-page {
    break-before: auto;
    orphans: 3;
    widows: 3;
  }

  /*       */
  .sub-page {
    break-before: auto;
    orphans: 3;
    widows: 3;
  }

  .print-page {
    margin: 0;
    padding-bottom: 8px; /*  margin-bottom */
  }
}

.btn-cancel {
  padding: 8px 16px;
  font-size: 14px;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 10px;
}

.btn-cancel:hover {
  background: #c82333;
}

.btn-cancel:disabled {
  background: #a71d2a;
  cursor: not-allowed;
}
</style>